import React from 'react';

const Jumbotitle = () => (
  <div className="jumbotron jumbotitle">
  <br/>
  <br/>
    <h1>Clicky Monkey</h1>
    <p>Click on an image to earn points, but do not click on any more than once!</p>
  </div>
);
export default Jumbotitle;



